
import Vue from "vue"
import { Prop } from "vue/types/options"
import { arrays } from "@/utils"
import { TableColumn } from "@/models"

export default Vue.extend({
    name: "cTable",
    props: {
        isFullWidth: {
            type: Boolean,
            default: false,
        },
        title: {
            type: String,
            default: "",
        },
        columns: {
            type: Array as Prop<TableColumn[]>,
            default: (): TableColumn[] => [],
        },
        items: {
            type: Array as Prop<any[]>,
            default: (): any[] => [],
        },
        needSorted: {
            type: Boolean,
            default: false,
        },
        align: {
            type: String,
            default: "left",
        },
    },
    data() {
        const sortOrders = {} as { [key: string]: number }
        this.columns.forEach((c) => {
            sortOrders[c.key] = 1
        })
        return {
            sortKey: this.columns[0].key,
            sortOrders,
        }
    },
    computed: {
        hasCaption(): boolean {
            return (this.title ?? "").length > 0
        },
        preparedItems(): any[] {
            const order = this.sortOrders[this.sortKey] ?? 1
            return this.needSorted
                ? arrays.clone(this.items).sort((a, b) => {
                    a = parseInt(a[this.sortKey]) !== NaN ? parseInt(a[this.sortKey]) : a[this.sortKey]
                    b = parseInt(b[this.sortKey]) !== NaN ? parseInt(b[this.sortKey]) : b[this.sortKey]

                    return (a === b ? 0 : a > b ? 1 : -1) * order
                })
                : this.items
        },
    },
    methods: {
        sortBy(key: string) {
            if (this.needSorted) {
                this.sortKey = key
                this.sortOrders[key] = this.sortOrders[key] * -1
            }
        },
    },
})
