
import Vue from "vue"

export default Vue.extend({
    name: "cArrowSVG",
    props: {
        isUp: {
            type: Boolean,
            default: false,
        },
        color: {
            type: String,
            default: "#818ca9",
        },
        hoverColor: {
            type: String,
            default: "#818ca9",
        },
        size: {
            type: String,
            default: "m",
            validator: (v: string) => ["s", "m", "l"].includes(v),
        },
        mode: {
            type: String,
            default: "normal",
            validator: (v: string) => ["normal", "circle"].includes(v),
        },
        isNoShadow: {
            type: Boolean,
            default: false,
        },
        styled: {
            type: String,
            default: "normal",
            validator: (v: string) => ["normal", "primary"].includes(v),
        },
    },
    computed: {
        listeners(): Record<string, Function | Function[]> {
            return {
                ...this.$listeners,
                click: this.click,
            }
        },
    },
    methods: {
        click(e: MouseEvent) {
            this.$emit("click", e)
        },
    },
})
