
import Vue from "vue"

export default Vue.extend({
    name: "cEdit",
    model: {
        prop: "value",
        event: "update:value",
    },
    props: {
        cols: {
            type: Number,
            default: 20,
        },
        disabled: {
            type: Boolean,
            default: false,
        },
        readonly: {
            type: Boolean,
            default: false,
        },
        isClear: {
            type: Boolean,
            default: false,
        },
        isConfirmationCode: {
            type: Boolean,
            default: false,
        },
        isError: {
            type: Boolean,
            default: false,
        },
        isLazy: {
            type: Boolean,
            default: false,
        },
        isPassword: {
            type: Boolean,
            default: false,
        },
        isShowIcon: {
            type: Boolean,
            default: false,
        },
        isValid: {
            type: Boolean,
            default: false,
        },
        isWarning: {
            type: Boolean,
            default: false,
        },
        mode: {
            type: String,
            default: "singleline",
            validator: (v: string) => ["singleline", "multiline"].includes(v),
        },
        placeholder: {
            type: String,
        },
        required: {
            type: Boolean,
            default: false,
        },
        resize: {
            type: String,
            default: "vertical",
            validator: (v: string) => ["vertical", "horizontal", "none", "both"].includes(v),
        },
        rows: {
            type: Number,
            default: 1,
        },
        type: {
            type: String,
            default: "text",
            validator: (v: string) => ["text", "number", "password", "email", "tel"].includes(v),
        },
        value: {
            type: String,
            default: "",
        },
        codeLength: {
            type: Number,
            default: 6,
        },
        inputmode: {
            type: String,
            default: "text",
        },
        isHideStatus: {
            type: Boolean,
            default: false,
        },
        isSearch: {
            type: Boolean,
            default: false,
        },
        isSearchLeft: {
            type: Boolean,
            default: false,
        },
        isSearchIconBold: {
            type: Boolean,
            default: true,
        },
        iconName: {
            type: String,
        },
        iconColor: {
            type: String,
            default: "#3F51B5",
        },
        autocomplete: {
            type: String,
            default: "off",
        },
        isHideRequired: {
            type: Boolean,
            default: false,
        },
    },
    data() {
        return {
            text: "",
            isShowPassword: false,
            isFocus: false,
            hasStatusText: false,
            formattedPhone: "",
            formattedCode: "",
            code: Object.fromEntries(Array.from({ length: this.codeLength }, (_, i) => [i + 1, ""])) as { [key: number]: string },
        }
    },
    computed: {
        iconTitle(): string {
            return this.isShowPassword ? "Скрыть пароль" : "Показать пароль"
        },
        isMultiline(): boolean {
            return this.mode === "multiline"
        },
        isPhone(): boolean {
            return this.type === "tel"
        },
        passwordMode(): string {
            return this.isShowPassword ? "text" : "password"
        },
        listeners(): Record<string, Function | Function[]> {
            return {
                ...this.$listeners,
                input: this.input,
                change: this.change,
                focus: this.focus,
                blur: this.blur,
                keydown: this.keyDown,
            }
        },
        multilineBinds(): any {
            const binds = [
                { style: this.resize },
                { required: this.required },
                { disabled: this.disabled },
                { readonly: this.readonly },
                { rows: this.rows },
                { cols: this.cols },
                {
                    class: {
                        error: this.isError,
                        warning: this.isWarning,
                    },
                },
                { autocomplete: this.autocomplete },
                { ref: "input" },
                { inputmode: this.inputmode },
            ] as any

            if (!this.isLazy) {
                binds.push({ value: this.value })
            }

            return binds
        },
        singlelineBinds(): any {
            const binds = [
                { type: this.isPassword ? this.passwordMode : this.type },
                { required: this.required },
                { disabled: this.disabled },
                { readonly: this.readonly },
                {
                    class: {
                        code: this.isConfirmationCode,
                        phone: this.isPhone,
                        password: this.isPassword,
                        error: this.isError,
                        warning: this.isWarning,
                        entered: this.value.length > 0 || this.formattedPhone.length > 0,
                    },
                },
                { autocomplete: this.autocomplete },
                { ref: "input" },
                { inputmode: this.inputmode },
            ] as any

            if (!this.isPhone) {
                binds.push({ value: this.value })
            }

            return binds
        },
    },
    watch: {
        value: {
            immediate: true,
            handler() {
                this.text = this.value
                if (this.isPhone) {
                    if ((this.value || "").length !== 0) {
                        this.phoneFormatting("value")
                    }
                }
            },
        },
    },
    mounted() {
        this.checkSlots()
    },
    updated() {
        this.checkSlots()
    },
    methods: {
        blur(e: FocusEvent) {
            this.isFocus = false
            this.$emit("blur", e)
        },
        change(e: Event) {
            if (this.isLazy) {
                this.$emit("update:value", this.text)
            }
            this.$emit("change", e)
        },
        checkSlots() {
            this.hasStatusText = !!this.$slots["status-text"]
        },
        clear() {
            this.$emit("update:value", "")
            if (this.isPhone) {
                this.formattedPhone = ""
            }
            this.$emit("input", "")
            this.isFocus = false
            setTimeout(() => {
                (this.$refs.input as HTMLInputElement).blur()
            }, 100)
        },
        focus(e: FocusEvent) {
            this.isFocus = true
            this.$emit("focus", e)
        },
        input(e: InputEvent) {
            if (this.isPhone) {
                if (e.inputType === "deleteContentBackward" && this.formattedPhone.length < 1) {
                    this.$emit("update:value", "")
                    return
                }

                this.phoneFormatting("formattedPhone")
                this.$emit("input", e)

                return
            }

            if (this.isConfirmationCode) {
                const index = Number((e.target as HTMLInputElement).dataset.id)
                this.code[index] = this.code[index].replace(/\D/g, "")
                const refs = this.$refs["ui-edit"] as HTMLInputElement

                if (this.code[index].length > 0 && index !== Object.keys(this.code).length) {
                    const next = refs.querySelector(`input[data-id="${index + 1}"]`) as HTMLElement
                    next.focus()
                }

                const result = Object.values(this.code).reduce((a, b) => a + b)
                this.$emit("update:value", result)

                return
            }

            if (!this.isLazy) {
                this.$emit("update:value", (e.target as HTMLInputElement | HTMLTextAreaElement).value)
            }
            this.$emit("input", e)
        },
        keyDown(e: KeyboardEvent) {
            if (this.isConfirmationCode) {
                const index = Number((e.target as HTMLInputElement).dataset.id)
                const refs = this.$refs["ui-edit"] as HTMLInputElement
                const prev = refs.querySelector(`input[data-id="${index - 1}"]`) as HTMLElement
                const next = refs.querySelector(`input[data-id="${index + 1}"]`) as HTMLElement

                if (e.code === "Backspace") {
                    if (this.code[index].length > 0) {
                        this.code[index] = ""
                        setTimeout(() => {
                            if (index !== 1) {
                                prev.focus()
                            }
                        }, 1)
                    } else {
                        if (index !== 1) {
                            this.code[index - 1] = ""

                            prev.focus()
                        }
                    }

                    const result = Object.values(this.code).reduce((a, b) => a + b)
                    this.$emit("update:value", result)
                }

                if (e.code === "ArrowRight" && index !== Object.keys(this.code).length) {
                    next.focus()
                }

                if (e.code === "ArrowLeft" && index !== 1) {
                    prev.focus()
                }
            }
            this.$emit("keydown", e)
        },
        phoneFormatting(phone: keyof { formattedPhone: string; value: string }) {
            let p = this[phone].replace(/\D/g, "")

            if (p.length > 10 && ["7", "8"].includes(p[0])) {
                p = p.slice(1)
            }

            const m = p.match(/(\d{0,3})(\d{0,3})(\d{0,2})(\d{0,2})/)
            if (m === null) {
                return
            }

            this.formattedPhone = !m[2] ? m[1] : m[1] + " " + m[2] + (m[3] ? "-" + m[3] : "") + (m[4] ? "-" + m[4] : "")
            this.$emit("update:value", m[0])
        },
        showPassword() {
            this.isShowPassword = !this.isShowPassword
        },
    },
})
